
.formContainer{
    /* width:25vw; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.inputItems{
    display: flex;
    flex-direction: column;
    width:13.5vw;
}
form{
   
    width:30vw;
}
.mainFormBox{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 15px;
    grid-row-gap:15px;
    margin-bottom: 20px;
}
.formContainer label{
    font-family: 'Inter';
font-style: normal;
font-weight: 600;
font-size: 14px;
line-height: 19px;
display: block;
}
.inputItems input{
   
height: 38px;

font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 12px;
display: flex;
justify-content: center;
align-items: center;
line-height: 17px;
padding:0 12px ;
color: #9E9A9A;

border: 1px solid #746D6D;
border-radius: 5px;
}
.formContainer select{
    height:38px;
    color: #9E9A9A;
    font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 12px;
border: 1px solid #746D6D;
    border-radius: 5px;
    padding: 0 10px;
  
}
.radio{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 8px 0;

}
.radio input{
    width: 22px;
    height: 25px;
    border: 3px solid #746D6D;
}
.checkboxContainer-Left{
    display: flex;
    justify-content: center;
    margin-right:50px;
    align-items: center;
    border: 1px solid #746D6D;
border-radius: 5px;

}
.checkboxContainer-Left img{
    margin:0 8px;
}
.checkboxContainer2{
    display: flex;
    margin-top:15px;
    width: 29vw;
    /* justify-content: space-between; */
    font-size: 15px;
    align-items: center;
    font-style: normal;
    font-weight: 600;
    flex-wrap: wrap;

}
.checkboxContainer2 input{
    margin:0 8px;
    
}
